@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
html {
    font-family: 'Orbitron', sans-serif;
    font-size: 10px;
    color: #fff;
    background-color: rgb(250, 96, 96)
}

.oneLine {
    display: flex;
    width: 50%;
    justify-content: space-around;
    margin: 0 auto;
}

.wrapper {
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    font-size: 2.7rem;
}

.center {
    display: flex;
    justify-content: center;
}

.tomato {
    margin: 0 auto;
    width: 500px;
    height: 500px;
    background-image: url("https://cdn.pixabay.com/photo/2012/04/18/14/37/tomato-37219_1280.png");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}

#timer-label {
    margin-top: 80px;
    margin-bottom: 0;
    font-size: 3.5rem;
    letter-spacing: 0.2rem;
    padding: 0;
}

#time-left {
    font-size: 4.5rem;
}

.icon {
    font-size: 2.5rem;
    padding: 5px;
}



